/* eslint-disable no-plusplus */
import React, { useEffect, useState } from 'react';
// import { useDispatch } from 'react-redux';
// import { getListVoucherByTypeHandle } from 'src/redux/actions/voucher.action';
import { connect, useSelector, useDispatch } from 'react-redux';
import { getListSelectedSelector } from 'src/redux/selectors/order.selector';
import { getAccessTokenSelector } from 'src/redux/selectors/auth';
import { addModal } from 'src/redux/actions/ui.action';
import { bindActionCreators } from 'redux';
import {
  addListSelectedHandle,
  getListFoodsMenuHandle,
  updateListSelectedHandle,
} from 'src/redux/actions/order.action';
import { MODAL_TYPE } from 'src/constants';
import ListOrderView from './ListOrder.view';

const ListOrderContainer = (props) => {
  const dispatch = useDispatch();
  const branch = props?.location?.state?.branch || {};
  const type = props?.location?.state?.type || {};
  const [listData, setListData] = useState({ page: 1, size: 12, data: [] });
  const accessToken = useSelector(getAccessTokenSelector);

  const onBookingClick = () => {
    if (accessToken) {
      dispatch(
        addModal({
          type: MODAL_TYPE.MODAL_BOOKING_FOOD,
          order: props.listSelected,
        })
      );
    } else {
      dispatch(
        addModal({
          type: MODAL_TYPE.FORCE_USER_LOGIN,
          order: props.listSelected,
        })
      );
    }
  };
  // const [listData, setListData] = useState({
  //   data: [],
  //   page: 1,
  //   size: 9,
  //   total: 0,
  // });
  // const dispatch = useDispatch();
  useEffect(() => {
    // const branch = props?.location?.state?.branch || {};
    if (branch?.id) {
      dispatch(
        getListFoodsMenuHandle(
          listData.page,
          listData.size,
          { ...type, branch_id: branch.id },
          (obj) => {
            const tempListData = {
              ...listData,
              data: obj.menu,
              total: obj.total,
            };

            setListData(tempListData);
          }
        )
      );
    }
  }, []);
  console.log('branch', branch, props);

  return (
    <ListOrderView
      branch={branch}
      onBookingClick={onBookingClick}
      listMenu={listData}
      listSelected={props.listSelected}
      {...props}
      onAddItemClick={(data) => {
        if (!accessToken) {
          dispatch(
            addModal({
              type: MODAL_TYPE.FORCE_USER_LOGIN,
              order: props.listSelected,
            })
          );
        } else {
          props.addListSelectedHandle(data);
        }
      }}
      onSeeMoreClick={() => {
        if (branch?.id) {
          dispatch(
            getListFoodsMenuHandle(
              listData.page + 1,
              listData.size,
              { ...type, branch_id: branch.id },
              (obj) => {
                const tempListData = {
                  ...listData,
                  data: listData.data.concat(obj.menu),
                  total: obj.total,
                };

                setListData(tempListData);
              }
            )
          );
        }
      }}
    />
  );
};

const mapStateToProps = (state) => ({
  listSelected: getListSelectedSelector(state),
});

const mapDispatchToProps = (dispatch) =>
  bindActionCreators(
    { addListSelectedHandle, updateListSelectedHandle },
    dispatch
  );

export default connect(mapStateToProps, mapDispatchToProps)(ListOrderContainer);
